.Footer {
  font-family: "TsukimiRounded", "NotoSansKRLight", sans-serif;
  width: 100%;
  height: 80px;
  background-color: #ffffff;
  color: #000000;
  text-align: center;
  transition: background-color 0s, color 0s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Footer.dark-mode {
  background-color: #000000;
  color: #ffffff;
}

.Footer * {
  text-decoration: none;
  color: black;
}
.Footer.dark-mode * {
  text-decoration: none;
  color: white;
}

.FoonterContent {
  width: 864px;
  height: 100%;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.FooterText {
  padding: 32px;
}

.FooterButton {
  padding: 25px;
}
@media only screen and (max-width: 899px) {
  .FoonterContent {
    width: 100%;
    height: 100%;
  }
  .FooterText {
    padding-left: 8px;
    padding-right: 8px;
  }
}
