.Container {
  font-family: "TsukimiRounded", "NotoSansKR", sans-serif;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  margin: auto;
  padding: 80px;
  min-height: calc(100vh - 502px);
}
/* .contentTitle {
  margin-bottom: 4px;
}
.contentSubTitle {
  margin-bottom: 40px;
} */

.Container h2 {
  margin-bottom: 0.5rem;
  line-height: 2rem;
}

.Container p {
  margin-bottom: 1rem;
  line-height: 1.25rem;
}
.Container pre {
  margin-bottom: 1rem;
}
.Container li {
  margin-bottom: 1rem;
  line-height: 1.25rem;
}

.Container a:hover {
  color: #0d6efd;
}
.Container.dark-mode a:hover {
  color: #0d6efd;
}

.Container a:hover * {
  color: #0d6efd;
}
.Container.dark-mode a:hover * {
  color: #0d6efd;
}

.blk20 {
  height: 20px;
}
.blk40 {
  height: 20px;
}

.Container * * :not(code *):not(span *) {
  text-decoration: none;
  color: black;
}
.Container.dark-mode * * :not(code *):not(span *) {
  text-decoration: none;
  color: white;
}
.Container .codeHighlight {
  color: #d63384;
  word-wrap: break-word;
}
.Container.dark-mode .codeHighlight {
  color: #ff4ea6;
  word-wrap: break-word;
}

.Container a {
  text-decoration: none;
  color: black;
}
.Container.dark-mode a {
  text-decoration: none;
  color: white;
}

.qnaListTeamName {
  margin-top: 20px;
}
.qnaListQnAName {
  margin-top: 12px;
  margin-left: 12px;
}

@media only screen and (max-width: 767px) {
  .Container {
    padding: 16px;
    padding-top: 24px;
    padding-bottom: 24px;
    width: calc(100%-32px);
    min-height: calc(100vh - 208px);
  }
}
